.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    background-color: #282c34;
    padding: 5px;
    margin: 5px 0;
    border-radius: 10px;
    position: relative;
    font-family:monospace;
}

.media:hover {
    background-color: white;
    color: #000000;
}

.poster {
    border-radius: 10px;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subTitle {
    display: flex;
    justify-content: space-between;
}